export const convertToUTCObjectDate = date => {
  let dateObject = date;

  if (typeof date === 'string') {
    let dateString = date.split('T')[0];
    dateString = new Date(dateString).toISOString();
    dateString = dateString.split('T')[0].split('-');
    dateObject = new Date(dateString[0], dateString[1] - 1, dateString[2]);
  } else {
    // dateObject not supported yet.
    // return null;
  }

  return dateObject;
};



